.history {
  align-items: center;
  display: flex;
  position: relative;
  width: 100%;
}
.history .line--center {
  display: none;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}
@media screen and (min-width: 767px) {
  .history .line--center {
    display: block;
  }
}
.history .line-vertical--center {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.history-bg__animate {
  animation: pulseTwo 4s ease-in-out infinite;
}

@keyframes pulseTwo {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}
.history-bg__dashed {
  animation: dashOne 1s ease-in;
  stroke-dasharray: 170px;
  stroke-dashoffset: 340px;
}

@keyframes dashOne {
  0% {
    stroke-dashoffset: 170px;
  }
  100% {
    stroke-dashoffset: 340px;
  }
}
.history-bg__dashedTwo {
  animation: dash 2s linear forwards;
  animation-delay: 1s;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
}

@keyframes dash {
  from {
    stroke-dashoffset: 1200;
  }
  to {
    stroke-dashoffset: 0;
  }
}
.history-bg__text {
  animation: dashText 3s linear forwards;
  animation-delay: 2s;
  opacity: 0;
  position: relative;
  z-index: 100;
}

@keyframes dashText {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
