.tabs {
	width : 100%;
}

.tabs__container {
	display : none;
	width   : 100%;
}

.tabs__container.active {
	display : flex;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 10px;
	}

	@media screen and (min-width : $break-desktop-lg) {
		padding-bottom : 30px;
	}
}


//tabs investment-info
.tabs__head {

  width: 400px;
}

.tabs__list {
	align-items   : flex-start;
	display       : flex;
	margin-bottom : 24px;
	overflow-x    : auto;
    position: relative;
    width         : 100%;
    z-index: 100;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 38px;
	}
}

.tabs__button {
	align-items      : center;
	background-color : transparent;
	border           : none;
	border-radius    : 8px;
	color            : #DDD;
	cursor           : pointer;
	display          : flex;
	font-size        : 16px;
	font-weight      : 400;
	line-height      : 24px;
	padding          : 10px 10px;
	text-decoration  : none;
	transition       : background-color .3s ease, color .3s ease;
	white-space      : nowrap;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		width : max-content;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:hover {
		background-color : #1E1E1E;
		color            : #FFF;
	}

	&.active {
		background-color : #1E1E1E;
		color            : $green;
	}

	&:not(:first-child) {
		margin-left : 24px;
	}

	span {
		display        : block;
		pointer-events : none;
	}
}


.tabs__wrap {
	overflow-x : auto;
}

.tabs__wrapper {
	color      : $white;
	max-height : 440px;
	max-width  : 100%;
	overflow-x : auto;
	overflow-y : auto;
	width      : 100%;


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		max-height    : 280px;
		padding-right : 10px;
	}

	@media screen and (min-width : $break-desktop-fh) {
		max-height : 440px;
	}
}
