.footer {
	bottom   : 0;
	left     : 0;
	position : absolute;
	width    : 100%;

	.line--top {
		top : 0;
	}

}

.footer--main {
	.footer__wrapper {
		height : 0;

		@media screen and (min-width : $break-table) {
			height : 48px;
		}

		@media screen and (min-width : $break-desktop) {
		}
		@media screen and (min-width : $break-desktop-lg) {
			height : 68px;
		}
	}
}

.footer__wrapper {
	padding  : 6px 14px;
	position : relative;

	@media screen and (min-width : $break-table) {
		padding : 8px 14px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 4px 14px;
	}
	@media screen and (min-width : $break-desktop-fh) {
		padding : 14px 14px
	}

	&:before {
		background-image  : url("../images/icon/corners-left-bottom.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		content           : "";
		height            : 4px;
		left              : 0;
		position          : absolute;
		top               : 0;
		width             : 5px;
		z-index           : 11;
	}

	&:after {
		background-image  : url("../images/icon/corners-right-bottom.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		content           : "";
		height            : 4px;
		position          : absolute;
		right             : 0;
		top               : 0;
		width             : 5px;
		z-index           : 11;
	}
}

.footer__wrap {
	display : none;
	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}
	@media screen and (min-width : $break-desktop) {

	}
	@media screen and (min-width : $break-desktop-lg) {

	}
}

.footer__left {
	align-items : center;
	display     : flex;
}

.footer__right {
	align-items : center;
	display     : flex;
}

.coin-exchange {
	display        : flex;
	flex-direction : column;

	&:not(:first-child) {


		@media screen and (min-width : $break-table) {
			margin-left : 24px;
		}
		@media screen and (min-width : $break-desktop) {

		}
		@media screen and (min-width : $break-desktop-fh) {
			margin-left : 40px;
		}
	}

	&:nth-child(n+5) {
		display : none;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {

		}
		@media screen and (min-width : $break-desktop-fh) {
			display : block;
		}
	}

	span {
		display : block;
	}
}

.coin-exchange__title {
	color          : $neutral-gray;
	font-size      : 12px;
	font-weight    : 700;
	line-height    : 16px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 20px;
	}
	@media screen and (min-width : $break-desktop-fh) {

	}

}

.coin-exchange__value {
	color          : $white;
	font-size      : 12px;
	font-weight    : 700;
	line-height    : 16px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 20px;
	}
	@media screen and (min-width : $break-desktop-fh) {

	}

	&--down {
		color : $red;

		svg {
			fill : $red;
		}
	}

	&--up {
		color : $green;

		svg {
			fill      : $green;
			transform : rotate(180deg);
		}
	}
}

.coin-group {
	align-items : center;
	display     : flex;

	&:not(:first-child) {

		@media screen and (min-width : $break-table) {
			margin-left : 24px;
		}
		@media screen and (min-width : $break-desktop) {
			margin-left : 32px;
		}
		@media screen and (min-width : $break-desktop-fh) {

		}
	}

	&__name {
		color          : $neutral-gray;
		font-size      : 10px;
		font-weight    : 400;
		line-height    : 12px;
		margin-right   : 8px;
		max-width      : 34px;
		text-transform : uppercase;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			font-size    : 12px;
			line-height  : 16px;
			margin-right : 16px;
			max-width    : none;
		}
		@media screen and (min-width : $break-desktop-fh) {

		}
	}

	.coin-exchange {
		margin-left : 0;
	}
}

.price {
	color          : $white;
	font-size      : 12px;
	font-weight    : 700;
	line-height    : 16px;
	text-transform : uppercase;
	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

		font-size   : 14px;
		line-height : 20px;
	}
	@media screen and (min-width : $break-desktop-fh) {

	}

	&--down {
		color : $red;
	}

	&--up {
		color : $green;
	}
}
