.about {
	position : relative;
	width    : 100%;

	@media screen and (min-width : $break-table) {
		padding : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop-fh) {

	}

	.line-vertical--center {
		display   : none;
		left      : 50%;
		top       : 0;
		transform : translateX(-50%);

		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}
}

.about-image {
	display         : flex;
	justify-content : center;
	position        : relative;
	width           : 100%;

	@media screen and (min-width : $break-table) {
		margin-left : -15px;
		width       : 51%;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 0;
		width       : 100%;
	}

	@media screen and (min-width : $break-desktop-fh) {

	}

	&__wrapper {
		display  : flex;
		position : relative;
		@media screen and (min-width : $break-table) {
			height : 362px;
		}

		@media screen and (min-width : $break-desktop) {
			height : auto;
			width  : 80%;
		}

		img {
			height     : 100%;
			max-width  : none;
			object-fit : cover;
			width      : 100%;
		}
	}


	svg {
		height     : 100%;
		max-height : none;
		object-fit : cover;
		width      : 100%;
	}

	&__pic {
		left      : 50%;
		position  : absolute;
		top       : 50%;
		transform : translate(-50%, -50%);
		width     : 50%;
		z-index   : 1;
	}

	.line--center {
		left      : -1000px;
		top       : 50%;
		transform : translateY(-50%);
		width     : 1000%;
		z-index   : -1;

		@media screen and (min-width : $break-table) {
			display : none;
			left    : 0;
		}

		@media screen and (min-width : $break-desktop) {
			display : block;
		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}

	.line-vertical--center {
		display   : none;
		left      : 50%;
		transform : translateX(-50%);
		z-index   : -1;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display   : block;
			height    : 1000%;
			top       : 50%;
			transform : translate(-50%, -50%);
		}

		@media screen and (min-width : $break-desktop-fh) {
		}
	}
}

.about__wrapper {
	position : relative;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
	@media screen and (min-width : $break-desktop-fh) {

	}

	.b-btn--primary {

		display  : none;
		position : absolute;


		@media screen and (min-width : $break-table) {
			bottom  : 69px;
			display : flex;
			right   : 15px;
		}

		@media screen and (min-width : $break-desktop) {
			bottom : 75px;
			right  : 0;
		}
		@media screen and (min-width : $break-desktop-fh) {

		}
	}
}

.about__wrap {
	display         : flex;
	flex-direction  : column-reverse;
	justify-content : space-between;
	min-height      : 100vh;
	padding         : 48px 0;
	position        : relative;

	@media screen and (min-width : $break-table) {
		padding : 49px 0 58px 0;
	}

	@media screen and (min-width : $break-desktop) {
		//align-items     : center;
		flex-direction  : row;
		height          : 100vh;
      justify-content : space-between;
		min-height: 900px;
		padding         : 48px 0;
	}

	@media screen and (min-width : $break-desktop-lg) {
		padding : 64px 0;
	}
}

.about__main {
	padding  : 0 19px;
	position : relative;

	@media screen and (min-width : $break-table) {
		display : block;
		padding : 15px 0 0 0;
	}

	@media screen and (min-width : 1023px) {
		padding : 65px 15px 15px 0;
	}

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		flex-direction  : column;
		justify-content : center;
		max-width       : 655px;
		padding         : 55px 15px 15px 0;
	}

	@media screen and (min-width : $break-desktop-fh) {

	}

	.line-vertical--right {
		display   : none;
		height    : 1000%;
		right     : 0;
		top       : 50%;
		transform : translateY(-50%);

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : block;
		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}

	.line--top {
		display   : none;
		left      : 50%;
		top       : 0;
		transform : translateX(-50%);
		width     : 1000%;

		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}

	&:before {
		content  : "";
		display  : none;
		position : absolute;
		@media screen and (min-width : $break-table) {
			background-image  : url("../images/icon/corners-left-top.svg");
			background-repeat : no-repeat;
			background-size   : 5px 6px;
			display           : block;
			height            : 5px;
			left              : -15px;
			top               : -1px;
			width             : 5px;
			z-index           : 11;
		}

		@media screen and (min-width : $break-desktop) {


			background-image : url("../images/icon/corners-right-bottom.svg");
			bottom           : 0;
			left             : unset;
			right            : 0;
			top              : unset;
			z-index          : 12;
		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}

	&:after {
		content  : "";
		display  : none;
		position : absolute;

		@media screen and (min-width : $break-table) {
			background-image  : url("../images/icon/corners-right-top.svg");
			background-repeat : no-repeat;
			background-size   : 5px 6px;
			display           : block;
			height            : 5px;
			right             : -15px;
			top               : -1px;
			width             : 5px;
			z-index           : 11;
		}

		@media screen and (min-width : $break-desktop) {
			background-image : url("../images/icon/corners-right-top.svg");
			bottom           : 0;
			right            : 0;
			z-index          : 12;
		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}
}

.about__title {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 15px;
	}

	@media screen and (min-width : $break-desktop-fh) {
		margin-bottom : 24px;
	}

}

.about__text {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 20px;
	}

	@media screen and (min-width : $break-desktop-fh) {
		margin-bottom : 40px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 24px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 15px;
			}

			@media screen and (min-width : $break-desktop-fh) {
				margin-bottom : 40px;
			}
		}
	}
}

.about__swiper {
	margin : 0 -19px 40px -19px;

	@media screen and (min-width : 551px) {
		margin : 0 -6px 40px -6px;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 -6px 20px -6px;
	}

	@media screen and (min-width : $break-desktop-fh) {
		margin : 0 -6px 30px -6px;
	}
}

.about-card {
	margin   : 0 32px;
	position : relative;
	z-index  : 2;

	@media screen and (min-width : 551px) {
		margin : 0 12px;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop-fh) {

	}




	&__elipsis {
		position : absolute;
		right    : 0;
		top      : 0;
	}
}

.card {
  background-color: $gray-2;
  border-radius: 16px;
  padding-top: 7px;
  position: relative;

  &:after {
    border-radius    : 16px;
    content          : "";
    height           : 48px;
    left            : 50%;
    position         : absolute;
    top              : 0;
    transform: translate(-50%);
    width: calc(100% - 16px);
    z-index          : -1;
  }

  &--green {

    &:after {
      background-color : $green;
    }
  }

  &--yellow {
    &:after {
      background-color : $yellow;
    }
  }
  &--blue {
    &:after {
      background-color : #6F5BEB;
    }
  }
}

.card-about {
  margin   : 0 32px;
  position : relative;
  z-index  : 2;

  @media screen and (min-width : 551px) {
    margin : 0 12px;
  }

  &__text {
    color       : $neutral-gray;
    font-size   : 12px;
    font-weight : 400;
    line-height : 16px;

    @media screen and (min-width : $break-table) {
      font-size   : 14px;
      line-height : 20px;
    }

    @media screen and (min-width : $break-desktop) {

    }

    @media screen and (min-width : $break-desktop-fh) {
      font-size   : 16px;
      line-height : 24px;
    }

  }

  &__wrap {
    background            : $gray-2;
    border-radius         : 16px;
    padding               : 24px 42px 24px 24px;

    @media screen and (min-width : 551px) {
      padding : 24px 24px;
    }

    @media screen and (min-width : $break-table) {
      padding : 32px 32px;
    }

    @media screen and (min-width : $break-desktop) {
      padding : 20px 20px;
    }

    @media screen and (min-width : $break-desktop-fh) {
      padding : 32px 32px;
    }
  }
}


.about-average {
	align-items : center;
	display     : flex;
}

.about-average__count {
	align-items       : center;
	background-image  : url("../images/about/average-mob.png");
	background-repeat : no-repeat;
	background-size   : cover;
	color             : $green;
	display           : flex;
	font-size         : 24px;
	font-weight       : 500;
	height            : 56px;
	justify-content   : center;
	line-height       : 32px;
	min-width         : 74px;
	padding           : 10px 12px;
	text-transform    : uppercase;

	@media screen and (min-width : $break-table) {
		background-size : 99px 72px;
		font-size       : 32px;
		height          : 72px;
		line-height     : 40px;
		min-width       : 99px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop-fh) {
		background-image : url("../images/about/average.png");
		background-size  : 135px 83px;
		font-size        : 48px;
		height           : 83px;
		line-height      : 56px;
		min-width        : 135px;
	}
}

.about-average__text {
	color       : $neutral-gray;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;
	margin-left : 24px;
	max-width   : 150px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
		max-width   : 296px;
	}

	@media screen and (min-width : $break-desktop-fh) {

	}
}

.swiper--about {
	margin : 0 -12px;

	@media screen and (min-width : $break-table) {
		margin : 0 -5px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 -12px;
	}

	@media screen and (min-width : $break-desktop-fh) {

	}

	.swiper-wrapper {
		margin-bottom : 32px;

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 20px;
		}

		@media screen and (min-width : $break-desktop-fh) {
			margin-bottom : 32px;
		}
	}

	.swiper-slide {
		width : 100%;

		@media screen and (min-width : 551px) {

		}
		@media screen and (min-width : $break-desktop-fh) {

		}
	}
}
