
* {
	-webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}

html {
	box-sizing : border-box;
	height     : 100%;
}

*,
*::before,
*::after {
	box-sizing : inherit;
}

.page-wrapper {
	display        : flex;
	flex-direction : column;
	min-height     : 100%;
	overflow       : hidden;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	.line {
		animation : animateLine 2s ease-in;
	}

	.line-vertical {
		animation : animateLineHorizontal 1.5s ease-in;
	}

}


.main {
	display  : flex;
	flex     : 1 0 auto;
	position : relative;

	&:before {
		background-image  : url("../images/icon/corners-left-top.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		content           : "";
		height            : 4px;
		left              : 12px;
		position          : absolute;
		top               : 12px;
		width             : 5px;
		z-index           : 11;

		@media screen and (min-width : $break-table) {
			left : 48px;
			top  : 48px;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}

	&:after {
		background-image  : url("../images/icon/corners-right-top.svg");
		background-repeat : no-repeat;
		background-size   : 5px 4px;
		content           : "";
		height            : 4px;
		position          : absolute;
		right             : 12px;
		top               : 12px;
		width             : 5px;
		z-index           : 11;

		@media screen and (min-width : $break-table) {
			right : 48px;
			top   : 48px;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}
}

#my-scrollbar {
	bottom   : 0;
	left     : 0;
	position : fixed;
	right    : 0;
	top      : 0;
}


.os-theme-dark, .os-theme-light {
	--os-handle-border-radius           : 10px;
	--os-handle-interactive-area-offset : 4px;
	--os-padding-axis                   : 2px;
	--os-padding-perpendicular          : 2px;
	--os-size                           : 6px !important;
	--os-track-border-radius            : 10px;
}

.os-scrollbar .os-scrollbar-handle {
	background : $green !important;
}

.container {
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	padding            : 0 12px;
	width              : 100%;

	@media screen and (min-width : $break-table) {
		padding : 0 48px;
	}
	@media screen and (min-width : $break-desktop) {

	}
	@media screen and (min-width : $break-desktop-lg) {
		padding : 0 64px;
	}
}

.line {
	background-color : $dark-stroke;
	height           : 1px;
	position         : absolute;
	width            : 100%;
	z-index          : 10;
}

.line--top {
	left : 0;
	top  : 64px;
}

.line--top {
	left     : 0;
	position : absolute;
	top      : 12px;
	z-index  : 10;


	@media screen and (min-width : $break-table) {
		top : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}
	@media screen and (min-width : $break-desktop-lg) {
		top : 64px;
	}
}

.line--bottom {
	bottom   : 12px;
	left     : 0;
	position : absolute;
	z-index  : 10;

	@media screen and (min-width : $break-table) {
		bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}
	@media screen and (min-width : $break-desktop-lg) {
		bottom : 64px;
	}
}

.line-vertical {
	background-color : $dark-stroke;
	height           : 100%;
	position         : absolute;
	top              : 0;
	width            : 1px;
	z-index          : 10;

	&--left {
		left : 12px;
		@media screen and (min-width : $break-table) {
			left : 48px;
		}
		@media screen and (min-width : $break-desktop) {

		}
		@media screen and (min-width : $break-desktop-lg) {
			left : 64px;
		}
	}

	&--right {
		right : 12px;
		@media screen and (min-width : $break-table) {
			right : 48px;
		}
		@media screen and (min-width : $break-desktop) {

		}
		@media screen and (min-width : $break-desktop-lg) {
			right : 64px;
		}
	}

	&--center {
		left      : 50%;
		transform : translateX(-50%);
	}
}

.offset {
	padding : 0 15px;
}


@keyframes animateLine {
	0% {
		width : 0;
	}

	100% {
		width : 100%;
	}
}


@keyframes animateLineHorizontal {
	0% {
		height : 0;
	}


	100% {
		height : 100%;
	}
}
