.history {
	align-items : center;
	display     : flex;
	position    : relative;
	width       : 100%;

	.line--center {
		display   : none;
		left      : 50%;
		top       : 50%;
		transform : translate(-50%, -50%);
		z-index   : -1;

		@media screen and (min-width : $break-table) {
			display : block;
		}
	}

	.line-vertical--center {
		left      : 50%;
		top       : 50%;
		transform : translate(-50%, -50%);
	}
}

.history-bg__animate {
	animation : pulseTwo 4s ease-in-out infinite;
}

@keyframes pulseTwo {
	0% {
		opacity : 0;
	}

	50% {
		opacity : 0.5;
	}

	100% {
		opacity : 0;
	}
}

.history-bg__dashed {
	animation         : dashOne 1s ease-in;
	stroke-dasharray  : 170px;
	stroke-dashoffset : 340px;
}

@keyframes dashOne {

	0% {
		stroke-dashoffset : 170px;
	}


	100% {
		stroke-dashoffset : 340px;
	}
}

.history-bg__dashedTwo {
	animation         : dash 2s linear forwards;
	animation-delay   : 1s;
	stroke-dasharray  : 1000;
	stroke-dashoffset : 1000;
}


@keyframes dash {
	from {
		stroke-dashoffset : 1200;
	}
	to {
		stroke-dashoffset : 0;
	}
}


.history-bg__text {
	animation       : dashText 3s linear forwards;
	animation-delay : 2s;
	opacity         : 0;
	position        : relative;
	z-index         : 100;
}


@keyframes dashText {

	0% {
		opacity : 0;
	}


	100% {
		opacity : 1;
	}
}

.history__wrapper {
	display         : flex;
	flex-direction  : column;
	justify-content : space-between;
	min-height      : 100vh;
	padding         : 15px 0;
	position        : relative;
	width           : 100%;
	z-index         : 10;

	@media screen and (min-width : $break-table) {
		padding : 48px 0;
	}

	@media screen and (min-width : $break-desktop) {
		flex-direction  : row;
		height          : 100vh;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop-lg) {
		padding : 64px 0;
	}

	&:before {
		content  : "";
		display  : none;
		position : absolute;
		@media screen and (min-width : $break-table) {
			background-image  : url("../images/icon/corners-left-top.svg");
			background-repeat : no-repeat;
			display           : block;
			height            : 4px;
			left              : 0;
			top               : 50%;
			transform         : translateY(-50%);
			width             : 5px;
			z-index           : 11;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}

	&:after {
		content  : "";
		display  : none;
		position : absolute;

		@media screen and (min-width : $break-table) {
			background-image  : url("../images/icon/corners-right-top.svg");
			background-repeat : no-repeat;
			display           : block;
			height            : 4px;
			right             : 0;
			top               : 50%;
			transform         : translateY(-50%);
			width             : 5px;
			z-index           : 11;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}

}

.history__image {
	align-items     : center;
	display         : flex;
	flex-direction  : column;
	justify-content : center;
	position        : relative;
	width           : 100%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		width : 50%;
	}

	@media screen and (min-width : $break-desktop-lg) {

	}

	@media screen and (min-width : $break-desktop-fh) {

	}

	&:before {
		content  : "";
		display  : none;
		position : absolute;
		@media screen and (min-width : $break-table) {
			display : block;
			height  : 4px;
			width   : 5px;
			z-index : 11;
		}

		@media screen and (min-width : $break-desktop) {
			background-image : url("../images/icon/corners-right-top.svg");
			left             : unset;
			right            : 0;
			top              : 0;

		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}

	&:after {
		content  : "";
		display  : none;
		position : absolute;

		@media screen and (min-width : $break-table) {
			display : block;
			height  : 4px;
			width   : 5px;
			z-index : 11;
		}

		@media screen and (min-width : $break-desktop) {
			background-image : url("../images/icon/corners-right-bottom.svg");
			bottom           : 0;
			left             : unset;
			right            : 0;
		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}

}

.history__img {
	position : relative;
	z-index  : 1;
	@media screen and (min-width : $break-table) {
		width : 90%;
	}

	@media screen and (min-width : $break-desktop) {
		width : 592px;
	}

	@media screen and (min-width : $break-desktop-lg) {
		width : 655px;
	}

	@media screen and (min-width : $break-desktop-fh) {
		width : 892px;
	}

	img {
		height     : 100%;
		max-width  : none;
		object-fit : cover;
		width      : 98%;
	}

	.line--center {
		display   : block;
		left      : 50%;
		top       : 50%;
		transform : translate(-50%, -50%);
		width     : 1000%;
		z-index   : -1;
		@media screen and (min-width : $break-table) {
			display : none;
		}
	}
}

.history__bg {
	height    : 100%;
	left      : 49%;
	position  : absolute;
	top       : 50%;
	transform : translate(-50%, -50%);
	width     : 100%;
	z-index   : 12;
}

.history__main {
	margin-top     : auto;
	padding-bottom : 35px;
	padding-left   : 19px;
	padding-right  : 8px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 15px;
		padding-left   : 15px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-right : 15px;
		width         : 50%;
	}

	@media screen and (min-width : $break-desktop-fh) {
	}
}

.history__wrap {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-left : auto;

	}

	@media screen and (min-width : $break-desktop-fh) {
		max-width : 522px;
	}
}

.history__title {
	margin-bottom : 24px;
}

