.hero {
	display         : flex;
	flex-direction  : column;
	justify-content : end;
	padding         : 45px 0 30px 0;
	position        : relative;
	width           : 100%;

	@media screen and (min-width : $break-table) {
		padding : 80px 0 84px 0;
	}
	@media screen and (min-width : $break-desktop) {
		justify-content : center;
		padding         : 0;
	}
	@media screen and (min-width : $break-desktop-lg) {

	}

	.line-vertical--center {
		left      : 50%;
		transform : translateX(-50%);
	}

}

.hero__wrapper {
	display               : grid;
	grid-template-columns : repeat(3, 1fr);
	position              : relative;
	z-index               : 20;

	@media screen and (min-width : $break-table) {
		align-items           : flex-end;
		grid-template-columns : repeat(6, 1fr);
	}
	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
		padding-top     : 0;
	}
	@media screen and (min-width : $break-desktop-lg) {

	}
}

.hero__text {
	grid-column   : 3 span;
	margin-bottom : 17px;
	max-width     : 213px;

	@media screen and (min-width : $break-table) {
		grid-column   : 1/4;
		margin-bottom : 0;
		max-width     : 357px;
	}
	@media screen and (min-width : $break-desktop) {

	}
	@media screen and (min-width : $break-desktop-lg) {

	}
}

.hero-image {
	//left      : 50%;
	//position  : absolute;
	//top       : 50%;
	//transform : translate(-50%, -50%);
	grid-column   : 3 span;
	grid-row      : 1/1;
	//margin-left : -100px;
	margin-bottom : 136px;
	position      : relative;
	z-index       : -1;

	@media screen and (min-width : $break-table) {
		grid-column   : 6 span;
		margin-bottom : 80px;
	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		margin-left   : -100px;
	}
	@media screen and (min-width : $break-desktop-lg) {

	}


	.line--center {
		left      : 50%;
		top       : 50%;
		transform : translate(-50%, -50%);
		width     : 1000%;
		z-index   : -1;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {

		}
		@media screen and (min-width : $break-desktop-lg) {

		}
	}
}

.hero-image__svg {
	height     : 100%;
	object-fit : cover;
	width      : 100%;
}

.hero-image__wrapper {
	position : relative;
}

.hero-image__pic {
	left      : 50%;
	position  : absolute;
	top       : 50%;
	transform : translate(-50%, -50%);
	width     : 44%;
	z-index   : 1;

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}


.b-btn--primary {
	grid-column : 3 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6/7;
	}
	@media screen and (min-width : $break-desktop) {

	}
	@media screen and (min-width : $break-desktop-lg) {

	}

	span {
		height    : 32px;
		min-width : 167px;
		@media screen and (min-width : $break-table) {
			height    : 48px;
			min-width : 201px;
		}
		@media screen and (min-width : $break-desktop) {

		}
		@media screen and (min-width : $break-desktop-fh) {
			min-width : 235px;
		}
	}
}

.hero__logo {
	height    : 55px;
	left      : 50%;
	position  : absolute;
	top       : 43%;
	transform : translate(-50%, -50%);
	width     : 1px;
	z-index   : 3;

  @media screen and (min-width : $break-table) {
    width     : 55px;
  }
}


@keyframes pulseLogo {
	0% {
		opacity : 0;
	}

	50% {
		opacity : 1;
	}

	100% {
		opacity : 0;
	}
}
