:root {
  --app-height : 100%;
}

html,
body {
  height: 100%;
}

body {
  background: $body-background;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: $body-color;
  font-family: $body-font;
  font-size: $body-font-size;
  height: 100vh;
  line-height: $body-line-height;
  margin: 0;
  min-width: 320px;
  overflow: hidden;
  padding: 0;
  position: relative;
  width: 100vw;


  @media not all and (hover: hover) {
    height: var(--app-height);
  }

}

// Images
img {
  border: 0;
  height: auto;
  max-width: 100%;
  vertical-align: top;
}

svg {
  max-height: 100%
}

select:not([multiple]) {
  vertical-align: middle;
}

// Table
table {
  border-collapse: separate;
  border-spacing: 0;
  max-width: 100%;
  width: 100%;
}

th {
  font-weight: $table-header-font-weight;
  text-align: left;
}

th,
td {
  line-height: inherit;
  padding: $table-cell-padding;
}

th {
  vertical-align: bottom
}

td {
  vertical-align: top
}

// Text
a {
  color: $white;
  outline: 0;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: $heading-font-weight;
  line-height: $heading-line-height;
  margin: 0;
}

p {
  color: $neutral-gray;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  margin: 0;

  @media screen and (min-width: $break-table) {
    font-size: 14px;
    line-height: 20px;
  }
  @media screen and (min-width: $break-desktop) {

  }
  @media screen and (min-width: $break-desktop-lg) {
    font-size: 16px;
    line-height: 24px;
  }
}

.section-title {
  color: $neutral-gray;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  text-transform: uppercase;

  @media screen and (min-width: $break-table) {
    font-size: 32px;
    line-height: 40px;
  }
  @media screen and (min-width: $break-desktop) {

  }
  @media screen and (min-width: $break-desktop-fh) {
    font-size: 48px;
    line-height: 56px;
  }
}

dl,
ol,
ul {
  margin: $list-margin;
}

ol {
  list-style: decimal;
}

h1 {
  font-size: $h1;
}

h2 {
  font-size: $h2;
}

h3 {
  font-size: $h3;
}

h4 {
  font-size: $h4;
}

h5 {
  font-size: $h5;
}

hr {
  background: #DEDEDE;
  border: 0;
  display: block;
  height: 1px;
  margin: 30px 0;
}

::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.hidden-box {
  display: none;
  visibility: hidden;
}

iframe {
  border: 0;
  outline: 0;
}

.videoWrapper {
  height: 0;
  padding-bottom: 56.25%; /* 16:9 */
  position: relative;
}

.videoWrapper iframe {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.text-center {
  text-align: center;
}

header,
nav,
section {
  display: block;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}


@keyframes dropdown-display {
  0% {
    opacity: 0;
    transform: scale(.98) translateY(-.6em);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

@keyframes dropdown-display-up {
  0% {
    opacity: 0;
    transform: scale(.98) translateY(.6em);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

blockquote {
  color: #000;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 189%;
  margin: 42px 0;
  max-width: 830px;
  padding-left: 27px;
  position: relative;
  @media screen and (min-width: $break-table) {
    margin: 28px 0;
  }
  @media screen and (min-width: $break-desktop) {
    margin: 48px 0;
  }

  &:after {
    background: #000;
    bottom: 5px;
    content: '';
    left: 0;
    position: absolute;
    top: 5px;
    width: 3px;
  }
}
