.rates {
	align-items         : center;
	background-position : center center;
	background-repeat   : no-repeat;
	display             : flex;
	position            : relative;
	width               : 100%;

	@media screen and (min-width : $break-desktop) {
		background-image : url("../images/rates/rates-bg.webp");

		.no-webp & {
			background-image : url("../images/rates/rates-bg.png");
		}
	}

	.line--center {
		display : none;
		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}

	.line-vertical--center {
		display : none;
		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}
}

.rates__wrapper {
	align-items     : flex-start;
	display         : flex;
	flex-direction  : column;
	justify-content : flex-end;
	min-height      : 100vh;
	padding         : 77px 19px 35px 19px;
	position        : relative;
	width           : 100%;
	z-index         : 10;

	@media screen and (min-width : $break-table) {
		padding : 48px 15px;
	}

	@media screen and (min-width : $break-desktop) {
		min-height : 100vh;
	}

	@media screen and (min-width : $break-desktop-lg) {
		padding : 64px 15px;
	}

	&:before {
		background-image  : url("../images/icon/corners-left-top.svg");
		background-repeat : no-repeat;
		content           : "";
		display           : none;
		height            : 4px;
		left              : 0;
		position          : absolute;
		top               : 50%;
		transform         : translateY(-50%);
		width             : 5px;
		z-index           : 11;

		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}

	&:after {
		background-image  : url("../images/icon/corners-right-top.svg");
		background-repeat : no-repeat;
		content           : "";
		display           : none;
		height            : 4px;
		position          : absolute;
		right             : 0;
		top               : 50%;
		transform         : translateY(-50%);
		width             : 5px;
		z-index           : 11;

		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}
}

.rates__wrap {
    width: 100%;
	@media screen and (min-width : $break-desktop) {
		padding-top : 140px;
	}

	@media screen and (min-width : $break-desktop-fh) {
		padding-top : 0;
	}
}

.rates__line {
	animation        : animateLineHorizontal 2s ease-in;
	background-color : $dark-stroke;
	bottom           : 0;
	display          : none;
	position         : absolute;
	top              : 0;
	width            : 1px;
	z-index          : -1;

	@media screen and (min-width : $break-desktop) {
		display : block;
	}

	&--left {
		left : 33.3%;
	}

	&--right {
		right : 33.3%;
	}
}

.rates__head {
	display               : grid;
	grid-template-columns : repeat(3, 1fr);
	margin-bottom         : 32px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop-fh) {
		margin-bottom : 32px;
	}

}

.rates__title {
	grid-column   : 1/3;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		grid-column   : 1/5;
		margin-bottom : 0;
	}

	@media screen and (min-width : 1024px) {
		margin-bottom : 30px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop-fh) {

	}
}

.rates__text {
	grid-column : 1/3;
	opacity     : 0.5;


	@media screen and (min-width : $break-table) {
		grid-column : 7/12;
	}

	@media screen and (min-width : 1024px) {
		grid-column : 1/6;
		text-align  : left;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 5/9;
	}

	@media screen and (min-width : $break-desktop-fh) {
		grid-column : 10/13;
		text-align  : right;
	}
}

.rates__swiper {
	padding-bottom : 15px;
	width          : 100%;
}

.card-rates {
	display  : flex;
	height   : auto;
	margin   : 0 19px;
	position : relative;
    width    : 100%;
	z-index: 2;

	@media screen and (min-width : $break-table) {
		margin : 0 15px;
	}

	&:before {
		border-radius : 20px;
		content       : "";
		height        : 48px;
		position      : absolute;
		right         : 8px;
		top           : 5px;
		width         : 48px;
		z-index       : -1;
	}

	&--test {
		&:before {
			background-color : $green;
		}
	}

	&--standard {
		&:before {
			background-color : $yellow;
		}
	}

	&--prem {
		&:before {
			background-color : #6F5BEB;
		}
	}
}

.card-rates__wrap {
	background-color      : #1E1E1E;
	border-radius         : 16px;
	display               : flex;
	flex-direction        : column;
	padding               : 24px 24px;
	width                 : 100%;
	z-index               : 2;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop-lg) {
		padding : 32px 32px;
	}

	@media screen and (min-width : $break-desktop-fh) {
		padding : 40px 40px;
	}

}

.card-rates__title {
	color          : $neutral-gray;
	font-size      : 18px;
	font-weight    : 500;
	line-height    : 24px;
	margin-bottom  : 12px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 18px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop-lg) {
		font-size     : 24px;
		line-height   : 32px;
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop-fh) {

	}
}

.card-rates__subtitle {
	color         : $neutral-gray;
	font-size     : 12px;
	font-weight   : 400;
	line-height   : 16px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size     : 14px;
		line-height   : 20px;
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 20px;
	}

	@media screen and (min-width : $break-desktop-lg) {

	}

	@media screen and (min-width : $break-desktop-fh) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.card-rates__group {
	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 40px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 20px;
		}
	}
}

.card-rates__name {
	color         : $neutral-gray;
	font-size     : 12px;
	font-weight   : 400;
	line-height   : 16px;
	margin-bottom : 4px;
	opacity       : 0.5;


	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop-lg) {

	}

	@media screen and (min-width : $break-desktop-fh) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.card-rates__descr {
	-webkit-box-orient : vertical;
	color              : $neutral-gray;
	display            : -webkit-box;
	font-size          : 12px;
	font-weight        : 400;
	line-height        : 16px;


	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop-lg) {

	}

	@media screen and (min-width : $break-desktop-fh) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.card-rates__price {
	color       : $neutral-gray;
	font-size   : 16px;
	font-weight : 700;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 18px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop-lg) {

	}

	@media screen and (min-width : $break-desktop-fh) {
		font-size   : 20px;
		line-height : 28px;
	}
}

.card-rates__bottom {
	display        : flex;
	flex-direction : column;
	margin-top     : auto;

	@media screen and (min-width : $break-table) {
		align-items     : center;
		flex-direction  : row;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop-lg) {

	}

	@media screen and (min-width : $break-desktop-fh) {

	}

	.card-rates__group {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
		}
	}
}


.swiper--rates {
	margin : 0 -19px;

	@media screen and (min-width : $break-table) {
		margin : 0 -15px;
	}

	.swiper-wrapper {
		flex-direction : column;
		@media screen and (min-width : $break-table) {
			flex-direction : row;
		}

		@media screen and (min-width : $break-desktop) {

		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}

	.swiper-slide {
		display : flex;
		height  : auto;
		width   : 100%;

		&:not(:last-child) {
			margin-bottom : 20px;
		}

		@media screen and (min-width : $break-table) {
			width : 100%;

			&:not(:last-child) {
				margin-bottom : unset;
			}
		}

		@media screen and (min-width : $break-desktop) {
			width : 33.33333%;

		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}

	.swiper-nav {
		display    : none;
		margin-top : 24px;

		@media screen and (min-width : $break-table) {
			display : flex;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}
}
