// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 400;
$btn-line-height           : 24px;
$btn-border-width          : 4px;
$btn-border-color          : $green;
$btn-padding               : 20px 24px 20px 27px;
$btn-font-size             : 14px;
$btn-border-radius         : 0;
$btn-transition            : background .3s ease, color .3s ease;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 14px 30px;
$btn-font-size-big         : 16px;
// Small
$btn-padding-small         : 8px 30px;
$btn-font-size-small       : 13px;

// COLOR

// Primary
$btn-primary-bg            : $pink-shade;
$btn-primary-color         : #FFF;
$btn-primary-color-outline : $btn-primary-bg;
$btn-primary-bg-hover      : $green;
$btn-primary-color-hover   : #FFF;


// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------


.b-btn {
	align-items        : center;
	background-color   : $green;
	border             : 4px solid $green;
	border-radius      : $btn-border-radius;
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	clip-path          : polygon(91% 0, 100% 52%, 100% 100%, 0 100%, 0 0);
	justify-content    : flex-start;
	line-height        : $btn-line-height;
	position           : relative;
	text-align         : center;
	text-decoration    : none;
	transition         : $btn-transition;
	width              : fit-content;


	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		background : inherit;
		box-shadow : none;
		cursor     : default;
		opacity    : .45;
	}

	&:active,
	&.active {

	}

	[class^="b-icon-"],
	[class*=" b-icon-"] {
		margin-right   : 10px;
		vertical-align : middle;
	}

	// SIZE
	// Big
	&--big {
		font-size : $btn-font-size-big;
		padding   : $btn-padding-big;
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// COLOR
	// Red
	&--primary {
		color : $btn-primary-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			span {
				background-color : $btn-primary-bg-hover;
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-primary-bg;
				color            : $btn-primary-color;
			}
		}

		span {
			align-items      : center;
			background-color : #102C03;
			bottom           : 4px;
			clip-path        : polygon(91% 0, 100% 52%, 100% 100%, 0 100%, 0 0);
			display          : flex;
			font-size        : 12px;
			height           : 40px;
			justify-content  : center;
			line-height      : 16px;
			min-width        : 235px;
			transition       : background-color .3s ease;

			@media screen and (min-width : $break-table) {
				font-size   : 14px;
				line-height : 20px;
			}
			@media screen and (min-width : $break-desktop-fh) {
				font-size   : 16px;
				height      : 56px;
				line-height : 24px;

			}
		}
	}

	// LOADING
	&--loading {
		&:before {
			animation        : loadingSpin 1s infinite linear;
			border           : 3px solid rgba($btn-loading-color, 0.6);
			border-radius    : 50%;
			border-top-color : $btn-loading-color;
			content          : '';
			display          : inline-block;
			height           : 12px;
			margin-left      : -15px;
			margin-right     : 10px;
			transition       : border .3s ease;
			vertical-align   : middle;
			width            : 12px;
		}

		&.outline {
			&:before {
				border           : 3px solid rgba($btn-loading-color-outline, 0.2);
				border-top-color : $btn-loading-color-outline;
			}

			&:hover {
				&:before {
					border           : 3px solid rgba($btn-loading-color, 0.6);
					border-top-color : $btn-loading-color;
				}
			}
		}
	}


	// SPACING
	& + .b-btn {
		margin-left : $btn-spacing-x;
	}
}

.b-btn-grouping {
	display : inline-block;

	.b-btn {
		border-radius : 0;
		float         : left;
		margin-right  : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			margin-left : -1px;
			z-index     : 1;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}


@keyframes pulse {
	0% {
		opacity : 0;
	}


	50% {
		opacity : 1;
	}

	100% {
		opacity : 0;
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}
