.quotes {
	align-items         : center;
	background-position : center center;
	background-repeat   : no-repeat;
	display             : flex;
	position            : relative;
	width               : 100%;
}

.quotes__wrapper {
	align-items     : flex-start;
	display         : flex;
	flex-direction  : column;
	justify-content : flex-end;
	min-height      : 100vh;
	padding         : 130px 15px 48px 15px;
	position        : relative;
	width           : 100%;
	z-index         : 30;

	@media screen and (min-width : $break-table) {
		padding : 48px 15px 48px 15px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop-fh) {
		padding : 64px 15px 64px 15px;
	}
}

.quotes__wrap {
	height      : 100%;
	padding-top : 20px;
	width       : 100%;
}

.quotes__title {
	margin-bottom : 40px;


	@media screen and (min-width : $break-table) {
		max-width : 352px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : none;
	}

	@media screen and (min-width : $break-desktop-fh) {
		max-width : 650px;
	}
}

.quotes-table {
	max-width  : none;
	//table-layout : fixed;
	text-align : right;
	width      : 768px;

	@media screen and (min-width : $break-table) {
		width : 1000px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 100%;
		width     : 100%;
	}

	@media screen and (min-width : $break-desktop-fh) {

	}

	thead {
		border-bottom  : 1px solid #222;
		color          : rgba(221, 221, 221, 0.5);
		font-size      : 12px;
		font-weight    : 400;
		line-height    : 16px;
		padding-bottom : 14px;
	}

	thead > tr > td {
		border-bottom : 1px solid #222;
		padding       : 0 0 14px 0;

		&:nth-child(2) {
			padding-left : 50px;
			text-align   : left;
		}
	}

	tbody {
		color       : $neutral-gray;
		font-size   : 14px;
		font-weight : 400;
		line-height : 20px;

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 24px;
		}
	}

	tbody > tr {
		transition : background-color .3s ease;

		&:hover {
			background-color : $dark-stroke;

		}
	}


	tbody > tr > td {
		border-bottom  : 1px solid #222;
		padding        : 20px 0;
		vertical-align : middle;

		@media screen and (min-width : $break-table) {

		}


		&:nth-child(1) {
			min-width  : 20px;
			text-align : left;
		}
	}
}

.quotes-coin {
	&__item {
		align-items : center;
		display     : flex;
	}

	&__name {
		margin-left : 16px;
	}
}

.quotes-daily {
	div {
		color          : $neutral-gray;
		font-size      : 12px;
		font-weight    : 400;
		line-height    : 16px;
		text-transform : uppercase;
	}
}

.quotes-value--down {
	color : #F30;

	svg {
		fill : #F30;
	}
}

.quotes-value--up {
	color : #3FC334;

	svg {
		fill      : #3FC334;
		transform : rotate(180deg);
	}
}
