.development {
	align-items : center;
	display     : flex;
	position    : relative;
	width       : 100%;

	.line--center {

	}

	&:before {
		content  : "";
		display  : none;
		position : absolute;
		@media screen and (min-width : $break-table) {
			background-image  : url("../images/icon/corners-left-top.svg");
			background-repeat : no-repeat;
			display           : block;
			height            : 4px;
			left              : 48px;
			top               : 50%;
			transform         : translateY(-50%);
			width             : 5px;
			z-index           : 11;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}

	&:after {
		content  : "";
		display  : none;
		position : absolute;

		@media screen and (min-width : $break-table) {
			background-image  : url("../images/icon/corners-right-top.svg");
			background-repeat : no-repeat;
			display           : block;
			height            : 4px;
			right             : 48px;
			top               : 50%;
			transform         : translateY(-50%);
			width             : 5px;
			z-index           : 10;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}

		@media screen and (min-width : $break-desktop-fh) {
		}
	}
}

.development__wrapper {
	align-items     : center;
	display         : flex;
	flex-direction  : column-reverse;
	justify-content : space-between;
	min-height      : 100vh;
	padding         : 14px 15px;
	position        : relative;
	width           : 100%;
	z-index         : 10;

	@media screen and (min-width : $break-table) {
		flex-direction : row;
		padding        : 48px 15px;
	}

	@media screen and (min-width : $break-desktop) {
		height     : 100vh;
		min-height : auto;
	}

	@media screen and (min-width : $break-desktop-lg) {
		padding : 64px 15px;
	}

}

.development__main {
	display         : flex;
	flex-direction  : column;
	height          : 100%;
	justify-content : space-between;
	padding         : 0 19px 35px 19px;
	position        : relative;

	@media screen and (min-width : $break-table) {
		justify-content : space-between;
		max-width       : 306px;
		min-height      : calc(100vh - 150px);
		padding         : 73px 0 15px 0;
	}

	@media screen and (min-width : 1024px) {
		max-width  : 485px;
		min-height : calc(100vh - 100px);
	}

	@media screen and (min-width : $break-desktop) {
		max-width  : 674px;
		min-height : calc(100vh - 150px);
		padding    : 55px 0 15px 0;
	}

	@media screen and (min-width : $break-desktop-fh) {
		max-width : 546px;
		padding   : 85px 0 15px 0;
	}

	.line-vertical--right {
		height    : calc(100% + 120px);
		right     : -15px;
		top       : 50%;
		transform : translateY(-50%);
	}

	&:before {
		content  : "";
		display  : none;
		position : absolute;
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			background-image  : url("../images/icon/corners-right-top.svg");
			background-repeat : no-repeat;
			display           : block;
			height            : 4px;
			left              : unset;
			right             : -15px;
			top               : 0;
			width             : 5px;
			z-index           : 11;
		}

		@media screen and (min-width : $break-desktop-fh) {

		}
	}

	&:after {
		content  : "";
		display  : none;
		position : absolute;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			background-image : url("../images/icon/corners-right-bottom.svg");
			bottom           : 0;
			display          : block;
			height           : 4px;
			right            : -15px;
			top              : unset;
			width            : 5px;
			z-index          : 11;
		}

		@media screen and (min-width : $break-desktop-fh) {
		}
	}
}

.development__title {
	margin-bottom : 24px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 15px;
	}

	@media screen and (min-width : $break-desktop-fh) {
		margin-bottom : 24px;
	}
}

.development__top {
	margin-bottom : 40px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
	}
}

.development__icon {
	margin-bottom : 24px;
}

.development__img {
	width : 85%;

	@media screen and (min-width : $break-table) {
		height    : 365px;
		position  : absolute;
		right     : 0;
		top       : 50%;
		transform : translateY(-50%);
		width     : 323px;
		z-index   : 10;
	}

	@media screen and (min-width : 1024px) {
		width : 395px;
	}

	@media screen and (min-width : 1180px) {
		height : 448px;
		width  : 518px;
	}

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		height          : auto;
		justify-content : center;
		left            : unset;
		position        : unset;
		top             : unset;
		transform       : translateY(0);
		width           : 100%;
	}

	@media screen and (min-width : $break-desktop-fh) {
		width : 68%;
	}
}

.development-image {
	height     : 100%;
	object-fit : cover;
	width      : 100%;
}

.development-img {
	position : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop-fh) {
		height : 834px;
		width  : 834px;
	}

	&__main {
		left      : 50%;
		position  : absolute;
		top       : 50%;
		transform : translate(-50%, -50%);
		z-index   : 2;


		@media screen and (min-width : $break-table) {
			width : 100%;
		}

		@media screen and (min-width : $break-desktop) {
			width : auto;
		}

		@media screen and (min-width : $break-desktop-fh) {
			height : 629px;
			width  : 589px;
		}

		img {
			height     : 100%;
			max-width  : none;
			object-fit : cover;
			width      : 100%;
		}
	}

	&__bg {
		animation : pulse 4s ease-in-out infinite;
		height    : 93%;
		left      : 49%;
		position  : absolute;
		top       : 49%;
      transform: translate(-50%,-50%);
      width: 97.5%;
		z-index   : 3;
	}
}

.development-img__wrap {
	position : relative;
}
